import React, { Component } from 'react';
import hridoylogo from "../../img/hridoylogo.jpg";
class Footer extends Component {
    render() {
        return (
            <React.Fragment>
                  <div class="rn-footer-area rn-section-gap section-separator">
        <div class="container">
            <div class="row">
                <div class="col-lg-12">
                    <div class="footer-area text-center">
                        <div class="logo">
                        <a href="index.html">
                                <img style={{width:'60px'}} src={hridoylogo} alt="logo" />
                            </a>
                        </div>
                        <p class="description mt--30">© 2022. All rights reserved by <a target="_blank">Md Hridoy</a></p>
                    </div>
                </div>
            </div>
        </div>
    </div>
    </React.Fragment>

        );
    }
}

export default Footer;