import React from 'react';
import port1 from '../../img/portfolio-01.jpg';
import port2 from '../../img/portfolio-02.jpg';
import port3 from '../../img/portfolio-03.jpg';
import port4 from '../../img/portfolio-04.jpg';
import port5 from '../../img/portfolio-05.jpg';
import port6 from '../../img/portfolio-06.jpg';
import Portfolio from './Portfolio';


const RealPortfolio = () => {
    const portfolio = [
      {  title:"react" , hearNumber: 23, body:"Restaurant Website", img:port1},
      {  title:"laravel" , hearNumber: 33,  body:"Blog Website", img:port2},
      {  title:"react" , hearNumber: 55,  body:"School Website", img:port3},
      {  title:"bootstrap" , hearNumber: 197,  body:"Restaurant Website", img:port4},
      {  title:"laravel" , hearNumber: 197,  body:"RealEstate", img:port5},
      {  title:"Bootstrap" , hearNumber: 197,  body:"Digital Marketin Website", img:port6},
      {  title:"react" , hearNumber: 23, body:"Restaurant Website", img:port1},
      {  title:"laravel" , hearNumber: 33,  body:"Blog Website", img:port2},
      {  title:"react" , hearNumber: 55,  body:"School Website", img:port3},
    ]
    return (
        <React.Fragment>

<div class="rn-portfolio-area rn-section-gap " id="portfolio">
            <div class="container">
                <div class="row">
                    <div class="col-lg-12">
                        <div data-aos="fade-up" data-aos-duration="500" data-aos-delay="100" data-aos-once="true" class="section-title text-center">
                            <span class="subtitle">Visit my portfolio and keep your feedback</span>
                            <h2 class="title">My Portfolio</h2>
                        </div>
                    </div>
                </div>

                <div class="row row--25 mt--10 mt_md--10 mt_sm--10">
                   


              
                    {
                        portfolio.map(portfolio => <Portfolio portfolio={portfolio}></Portfolio>) 
                  }

                   

                    
                
                </div>
            </div>
        </div>

          
        </React.Fragment>
    );
};

export default RealPortfolio;