import React from 'react';

const Portfolio = (props) => {
    const {title, hearNumber,body, img} = props.portfolio       
    return (
        <React.Fragment>
        <div data-aos="fade-up" data-aos-delay="500" data-aos-once="true" class="col-lg-6 col-xl-4 col-md-6 col-12 mt--50 mt_md--30 mt_sm--30">
        <div class="rn-portfolio" data-toggle="modal" data-target="#exampleModalCenter">
            <div class="inner">
                <div class="thumbnail">
                    <a href="javascript:void(0)">
                        <img src={img} alt="Personal Portfolio Images" />
                    </a>
                </div>
                <div class="content">
                    <div class="category-info">
                        <div class="category-list">
                            <a href="javascript:void(0)">{title}</a>
                        </div>
                        <div class="meta">
                            <span><a href="javascript:void(0)"><i class="feather-heart"></i></a>
                               {hearNumber}</span>
                        </div>
                    </div>
                    <h4 class="title"><a href="javascript:void(0)">{body}<i
                                class="feather-arrow-up-right"></i></a></h4>
                </div>
            </div>
        </div>
    </div>
    </React.Fragment>
    );
};

export default Portfolio;