import React from "react";
import webdesign from '../../img/webdesing.png';
import webdevelopment from '../../img/webdevelopment.png';
import wordpress from '../../img/wordpress.png';
import MiniService from './MiniService';
function MiniServiceMain() {
  return (
    <React.Fragment>
       

<div class="rn-service-area rn-section-gap section-separator" id="features">
            <div class="container">
                <div class="row">
                    <div class="col-lg-12">
                        <div class="section-title text-center" data-aos="fade-up" data-aos-duration="500" data-aos-delay="100" data-aos-once="true">
                            <span class="subtitle">Features</span>
                            <h2 class="title">My Services</h2>
                        </div>
                    </div>
                </div>
                <div class="row row--25 mt_md--10 mt_sm--10">
                
                    <div data-aos="fade-up" data-aos-duration="500" data-aos-delay="100" data-aos-once="true" class="col-lg-6 col-xl-4 col-md-6 col-sm-12 col-12 mt--50 mt_md--30 mt_sm--30">
                    <MiniService title="PSD TO HTML5" body="I will convert your PSD to HTML5 with Latest Bootstrap. It will fully responsive. Pixel perfect code and fresh hand code is my power. If you have any design or idea I can make it a website." img={webdesign} />
                    </div>
                    <div data-aos="fade-up" data-aos-duration="500" data-aos-delay="100" data-aos-once="true" class="col-lg-6 col-xl-4 col-md-6 col-sm-12 col-12 mt--50 mt_md--30 mt_sm--30">
                    <MiniService title="FRONT-END DEVELOPER" body="I’m a Front End Web developer who is passionate about making error-free websites with 100% client satisfaction. Last 5years I completed lots of front-end work" img={webdevelopment} />
                    </div>
                    <div data-aos="fade-up" data-aos-duration="500" data-aos-delay="100" data-aos-once="true" class="col-lg-6 col-xl-4 col-md-6 col-sm-12 col-12 mt--50 mt_md--30 mt_sm--30">
                    <MiniService title="WORDPRESS DEVELOPER" body="I love to develop WordPress websites. I developed more than 300 WordPress websites. It’s fun to create a WordPress website and you will be able to control the complete website from the admin panel." img={wordpress} />
                    </div>

                </div>
            </div>
        </div>
        </ React.Fragment>
                                         
   
  );
}

export default MiniServiceMain;
