import React from 'react';
import Footer from '../../Components/Footer/Footer';
import Header from '../../Components/Header/Header';
import PageHeader from '../../Components/PageHeader/PageHeader';
import certificate from '../../img/certificate.jpg';
import "./Certificate.css";
const Certificate = () => {
    return (
        <div>
              <Header/>
              <PageHeader headerpage="CERTIFICATE"/>
               <h1 className='cerh1'>First Grade</h1>
              <div className="row">
                  <div className="col-md-2"></div>
                  <div class="col-lg-8">
                        <div data-aos="fade-up" data-aos-duration="1000" data-aos-delay="100" data-aos-once="true" class="image-area">
                            <div class="thumbnail">
                                <img className='certifiImg' src={certificate} />
                            </div>
                        </div>
                    </div>
                  <div className="col-md-2"></div>
              </div>
          
          
               
              <Footer/>
        </div>
    );
};

export default Certificate;