import React, { Component } from 'react';
import { Container, Nav, Navbar } from 'react-bootstrap';
import { NavLink } from 'react-router-dom';
import hridoylogo from "../../img/hridoylogo.jpg";
import "./Header.css";
class Header extends Component {
    render() {
         
        return (
          <React.Fragment>
    <Navbar  expand="lg" sticky='top'>
  <Container>
    <Navbar.Brand className="menu" to="/"><div class="herimg"><img className='headerlogo' src={hridoylogo} alt="" /></div></Navbar.Brand>
    <Navbar.Toggle aria-controls="basic-navbar-nav" />
    <Navbar.Collapse id="basic-navbar-nav">
      <Nav className="ml-auto">
        <NavLink exact activeStyle={{color:'red'}}  className="menu"  to="/">Home</NavLink>
        <NavLink  activeStyle={{color:'red'}} className="menu"  to="/about">About</NavLink>
        <NavLink activeStyle={{color:'red'}} className="menu" to="/portfolio">Portfolio</NavLink>
        <NavLink activeStyle={{color:'red'}} className="menu" to="/service">Services</NavLink>
        <NavLink activeStyle={{color:'red'}} className="menu" to="/certificate">Certificate</NavLink>
        <NavLink activeStyle={{color:'red'}} className="menu" to="/contact">Contact Us</NavLink>
      </Nav>
    </Navbar.Collapse>
  </Container>
</Navbar>
</ React.Fragment>
        );
    }
}

export default Header;