import React from 'react';
import { Link } from 'react-router-dom';
import hridoy from "../../img/hri3.png";

const MiniAbout = () => {
    return (
        <React.Fragment>
        
        <div id="about" class="rn-about-area rn-section-gap">
            <div class="container">
  
                <div class="row">
                    <div class="col-lg-5">
                        <div data-aos="fade-up" data-aos-duration="1000" data-aos-delay="100" data-aos-once="true" class="image-area">
                            <div class="thumbnail">
                                
                        
                                <img src={hridoy} alt="Personal Portfolio Image" className='cerimg' />
                            </div>
                        </div>
                    </div>
                    <div data-aos="fade-up" data-aos-duration="1000" data-aos-delay="100" data-aos-once="true" class="col-lg-7 mt_sm--30">
                        <div class="contant">
                            <div class="section-title text-left">
                                <span class="subtitle">Why You Hire Me?</span>
                                <h2 class="title">About Me</h2>
                            </div>
                            <p class="discription">
                            Over the past years, I created 100s of websites for my clients. I pride myself on doing quality work and maintain excellent communication. Most of the time I work with WordPress but some technologies I enjoy working with include ReactJS, JavaScript as well as PHP.
                            </p>
                        
                            <Link class="rn-btn" to="/contact"><span>Hire Me</span></Link>
                        </div>
                    </div>
                </div>
            </div>
        </div>

        </ React.Fragment>
    );
};

export default MiniAbout;