import React from 'react';
import "./PageHeader.css";
const PageHeader = (props) => {
    return (
        <React.Fragment>
            <div class="page-header text-center py-5 text-white "><h3 style={{fontSize:'35px', padding:"20px 0px"}}>{props.headerpage}</h3></div>
            </React.Fragment>
    );
};

export default PageHeader;