import React from 'react';

const Skill = (props) => {
    const{ name, img} = props.skill
    return (
        <div>
              <li data-aos="fade-up" data-aos-duration="500" data-aos-delay="300" data-aos-once="true" title="angularJS">
                                    <img src={img} alt="skill" />
                 </li>
        </div>
    );
};

export default Skill;